                    :show-overflow-tooltip="true"
                    prop="name"
                    label="姓名">
                </el-table-column>
                <el-table-column
                    align="center"
                    label-align="center"
                    :show-overflow-tooltip="true"
                    prop="ystate"
                    label="状态">
                  <template slot-scope="scope">
                    <el-tag type="danger" v-if="scope.row.ystate==='未完成'">{{ scope.row.ystate }}</el-tag>
                    <el-tag type="success" v-else>{{ scope.row.ystate }}</el-tag>
                  </template>
                </el-table-column>
                <el-table-column
                    align="center"
