                    label="操作">
                  <template slot-scope="scope">
                    <el-button type="primary" @click="editYu(scope.row)">编辑</el-button>
                    <el-popconfirm title="确定删除吗？" @confirm="delYu(scope.row.id)">
                      <el-button slot="reference" type="danger" style="margin-left: 10px">删除</el-button>
                    </el-popconfirm>
                  </template>
                </el-table-column>
