                 @open="more++">
        <el-form :key="more" :model="form" :rules="rules" ref="form" label-width="100px">

          <el-form-item label="姓名" label-width="15%" prop="name">
            <el-input v-model="form.name" autocomplete="off" style="width: 70%" disabled></el-input>
          </el-form-item>
          <el-form-item label="状态" label-width="15%" prop="ystate">
            <el-radio-group v-model="form.ystate">
              <el-radio label="未完成">
                <el-tag type="danger">未完成</el-tag>
              </el-radio>
              <el-radio label="已完成">
                <el-tag type="success">已完成</el-tag>
              </el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="时间" label-width="15%" prop="ytime">
            <el-date-picker
                v-model="form.ytime"
                type="datetime"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="类型" label-width="15%" prop="ytype">
            <el-select v-model="form.ytype" placeholder="请选择">
              <el-option
