                v-model="form.yremark">
            </el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="CancelDialog">取 消</el-button>
          <el-button type="success" @click="resetRef">重置</el-button>
          <el-button type="primary" @click="submitYu()">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    <div>
      <el-dialog title="请填写信息" :visible.sync="dialogFormVisible1" width="30%" :before-close="CancelDialog"
                 @open="more1++">
        <el-form :key="more1" :model="form1" :rules="rules" ref="form1" label-width="100px">

          <el-form-item label="姓名" label-width="15%" prop="name">
            <el-input v-model="form1.name" autocomplete="off" style="width: 70%" disabled></el-input>
          </el-form-item>
          <el-form-item label="时间" label-width="15%" prop="btime">
            <el-date-picker
                v-model="form1.btime"
                type="datetime"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="类型" label-width="15%" prop="btype">
