            <el-input v-model="form1.wf" autocomplete="off" style="width: 70%" type="number"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="CancelDialog">取 消</el-button>
          <el-button type="success" @click="resetRef">重置</el-button>
          <el-button type="primary" @click="submitBl()">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
