<script>

import request from "@/utils/request";
import moment from "moment";

export default {
  name: 'HomeView',
  data() {
    return {
      ytime: '',
      cardTitle: '今日预约',
      tableData: [],
      params: {
        name: '',
        stime: moment().format('YYYY-MM-DD') + " 00:00:00",
        etime: moment().format('YYYY-MM-DD') + " 23:59:59"
      },
      cardTitle1: '今日未付',
      tableData1: [],
      dialogFormVisible: false,
      form: {},
      rules: {
        name: [
          {required: true, message: '请选择患者', trigger: blur}
        ],
        ystate: [
          {required: true, message: '请选择状态', trigger: blur}
        ],
        ytime: [
          {required: true, message: '请选择时间', trigger: blur}
        ],
        ytype: [
          {required: true, message: '请选择就诊类型', trigger: blur}
        ],
        yremark: [],
        btime: [
          {required: true, message: '请选择时间', trigger: blur}
        ],
        btype: [
          {required: true, message: '请选择就诊类型', trigger: blur}
        ],
        bdetail: [],
        fy: [
          {required: true, message: '请输入费用', trigger: blur}
        ],
        wf: [
          {required: true, message: '请输入未付费用', trigger: blur}
        ],
      },
      more: 0,
      dialogFormVisible1: false,
      form1: {},
      more1: 0,
      options: [{
        value: '洗牙',
        label: '洗牙'
      }, {
        value: '拔牙',
        label: '拔牙'
      }, {
        value: '补牙',
        label: '补牙'
      }, {
        value: '根管治疗',
        label: '根管治疗'
      }, {
        value: '固定义齿修复',
        label: '固定义齿修复'
      }, {
        value: '活动义齿修复',
        label: '活动义齿修复'
      }, {
        value: '种植牙',
        label: '种植牙'
      }, {
        value: '牙齿矫正',
        label: '牙齿矫正'
      }],
    }
  },
  created() {
    this.findYu()
    this.findBl()
  },
  methods: {
    findYu() {
      request.get("/findYu1", {
        params: this.params
      }).then(res => {
        if (res.state === 0) {
          this.tableData = res.data;
          this.$message({
            message: '查询成功',
            type: 'success'
          });
        } else {
          this.$message.error(res.message);
          this.$router.push("/login")
        }
      })
    },

    findBl() {
      request.get("/findBl1", {
        params: this.params
      }).then(res => {
        if (res.state === 0) {
          this.tableData1 = res.data;
          this.$message({
            message: '查询成功',
            type: 'success'
          });
        } else {
          this.$message.error(res.message);
          this.$router.push("/login")
        }
      })
    },

    findAllWf() {
      this.params.etime = '';
      this.params.stime = '';
      this.cardTitle1 = "所有未付";
      this.findBl()
    },

    getSE(val) {
      this.params.stime = val + ' 00:00:00';
      this.params.etime = val + ' 23:59:59';
    },

    editYu(obj) {
      this.form = obj;
      this.brname = false
      this.dialogFormVisible = true;
    },
    submitYu() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          request.post("/saveYu", this.form).then(res => {
            if (res.state === 0) {
              this.$message({
                message: '操作成功',
                type: 'success'
              });
              this.dialogFormVisible = false
              this.findYu()
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    },

    delYu(id) {
      request.delete("/delYu/" + id).then(res => {
        if (res.state === 0) {
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.findYu()
        } else {
          this.$message.error(res.message);
        }
      })
    },

    editBl(obj) {
      this.form1 = obj;
      this.dialogFormVisible1 = true;
    },
    submitBl() {
      this.$refs.form1.validate((valid) => {
        if (valid) {
          this.form1.name = null;
          request.post("/saveBl/" + this.form1.brid, this.form1).then(res => {
            if (res.state === 0) {
              this.$message({
                message: '操作成功',
                type: 'success'
              });
              this.dialogFormVisible1 = false
              this.findBl()
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    },

    delBl(id) {
      request.delete("/delBl/" + id).then(res => {
        if (res.state === 0) {
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.findBl()
        } else {
          this.$message.error(res.message);
        }
      })
    },

    resetRef() {
      this.$refs.form.resetFields();
    },

    CancelDialog() {
      this.dialogFormVisible = false;
      this.dialogFormVisible1 = false;
      this.resetRef();
    },
  },
  watch: {
    ytime: {
      handler(val) {
        val = moment(val).format('YYYY-MM-DD')
        //console.log(val)
        this.cardTitle = val + "的预约"
        this.cardTitle1 = val + "的未付"
        this.getSE(val)
        this.findYu()
        this.findBl()
      }
    }
  }
}
</script>

